import React, { Component } from 'react';
import Swipe from 'react-easy-swipe';

class Drawer extends Component {
    constructor(props){
        super(props);
        this.state = {
            elementHeight: 300,
            swipedY: 0
        }
    }

    componentDidMount() {
        const elementHeight = this.divElement.clientHeight;
        console.log(elementHeight, 'height');
        this.setState({ elementHeight });
    }

    onSwipeStart = (event) => {
        console.log('Start swiping...', event);
    }
    
    onSwipeMove = (position, event) => {
        if(Math.sign(position.y) === 1){
            this.setState({swipedY: position.y})
        }
    }
    
    onSwipeEnd = (event) => {
        const half = this.state.elementHeight / 2;
        if(this.state.swipedY > half){
            this.setState({swipedY: this.state.elementHeight})
            //hide the drawer
        } else {
            this.setState({swipedY: 0})
        }
    }

    render(){
        return (
            <div className="">
                <div className="fader"></div>
                <Swipe
                    onSwipeStart={this.onSwipeStart}
                    onSwipeMove={this.onSwipeMove}
                    onSwipeEnd={this.onSwipeEnd} >
                        <div className="drawer" ref={ (divElement) => { this.divElement = divElement } } style={{height: this.state.elementHeight - this.state.swipedY}}></div>
                </Swipe>
                

            </div>
        )
    }
}

export default Drawer;